@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Open Sans", "Helvetica", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  font-size: 1rem;
  --textColor: #fff;
  --colorLight: #e8e8e9;
  --colorRed: #910b1b;
  --colorRedDark: #630712;
  --colorGrey: #27272b;
  --bgGrey: #68676f;
  --bgDarker: #53545b;
  --bgMidDark: #4e4f57;
  --bgDarkest: #41434b;
  --bgEvenDarkest: #393a42;
  --bgLight: #b1afbb;
  --bgMidLight: #777580;

  --catA: teal;
  --catAtransp: rgba(0, 128, 128, 0.9);
  --catB: olive;
  --catBtransp: rgba(128, 128, 0, 0.9);
  --catC: salmon;
  --catCtransp: rgba(250, 128, 114, 0.9);
  --catD: palegoldenrod;
  --catDtransp: rgba(238, 232, 170, 0.9);
}

ul {
  list-style: none;
  margin: none;
  padding: none;
}

a {
  color: #fff;
  color: var(--textColor);
  text-decoration: none;
}

html,
body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: url(/static/media/bg.d88b4827.jpg) center var(--bgGrey);
  background-size: cover;
  margin: 0;
  padding: 0;
  height: 100vh;
  overflow: hidden;
}

.main-task {
  transform: translateY(-10vh);
  text-align: center;
}

.empty-h1 {
  max-width: 580px;
  margin: 20px auto -20px;
  margin-bottom: -20px;
  font-size: 2.5rem;
  font-weight: 700;
  color: #30343fd5;
  letter-spacing: -1.2px;
  text-shadow: 0px 1px 0px rgba(200, 200, 200, 0.5);
}

.empty-h3 {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--bgDarkest);
  text-shadow: 0px 1px 0px rgba(200, 200, 200, 0.5);
}

@media (max-width: 768px) {
  .empty-h1 {
    font-size: 1.7rem;
  }

  .empty-h3 {
    font-size: 1.2rem;
  }
}

::-webkit-scrollbar {
  width: 4px;
  background: var(--bgMidLight);
}

::-webkit-scrollbar-thumb {
  width: 4px;
  background: var(--bgMidDark);
  border-radius: 2px;
}

::-webkit-scrollbar-thumb:hover {
  width: 12px;
  background: var(--bgLight);
}

.topbar {
  display: flex;
  justify-content: space-between;
  align-items: left;
  background-color: var(--colorLight);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: calc(8px + 1vh);
  z-index: 2;
}

.logo {
  width: 65vw;
  max-width: 160px;
  margin: 5px 5px 0;
  opacity: 0.5;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  transition: all 0.5s ease-in-out;
}

.top-left:hover > .logo {
  opacity: 1;
  -webkit-filter: none;
          filter: none;
}

.top-left {
  flex-basis: content;
  padding: 0.8vh 1vh;
  margin: 0;
}

.top-center {
  flex-shrink: 1;
}

.top-right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 20px;
}

.top-right:hover,
.top-left:hover {
  cursor: pointer;
}

.top-right:hover > .hamburger,
.top-right:hover > .hamburger::after,
.top-right:hover > .hamburger::before {
  background: var(--bgDarker);
  max-width: 45px;
}

.hamburger,
.hamburger::after,
.hamburger::before {
  display: block;
  width: 10vw;
  max-width: 30px;
  height: 3px;
  border-radius: 3px;
  background: var(--bgLight);
  transition: all 0.5s ease-in-out;
}

.hamburger::after,
.hamburger::before {
  content: "";
  position: relative;
  transform: translateY(5px);
}

.hamburger::before {
  transform: translateY(-8px);
}

@media (max-width: 768px) {
  .top-center {
    display: none;
  }
}

.btn-small,
.grey-btn {
  background: var(--bgDarkest);
  height: 18px;
  padding: 3px 16px 1px;
  margin-left: 4px;
  border-radius: 5px;
  font-size: 0.75rem;
  font-weight: 600;
  text-align: center;
  color: white;
  text-transform: uppercase;
  transition: all 0.5s ease;
}

.btn-small:hover,
.grey-btn:hover {
  opacity: 1;
  padding: 3px 32px 1px;
  transition: all 0.3s ease;
  cursor: pointer;
}

.btn-red {
  background: var(--colorRed);
}

.btn-grey:hover {
  background: var(--colorGrey);
}

.task-list {
  margin-bottom: 20px;
}

.no-tasks {
  text-align: center;
  font-size: 1.3rem;
  font-weight: 700;
  letter-spacing: -0.5px;
  margin: 20px auto 100px;
  color: var(--bgEvenDarkest);
  text-shadow: 0px 1px 0px rgba(200, 200, 200, 0.3);
}

.task {
  position: relative;
  background: var(--bgDarkest);
  width: auto;
  padding: 4px 16px;
  margin: 2px 0 8px;
  border-radius: 5px;
  font-size: 15px;
  text-align: left;
  color: white;
  box-sizing: border-box;
  animation: task-bounce-in 1s;
}

.task p {
  font-family: "Open Sans", "Montserrat", sans-serif;
  width: 90%;
  /* background: rgba(53, 46, 46, 0.233); */
}

.task-edit {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(47, 49, 54, 0.9);
  top: 0;
  left: 0;
  border-radius: 5px;
  opacity: 0;
  transition: all 0.2s ease;
}

.task:hover > .task-edit {
  opacity: 1;
}

.task-btns {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding-right: 8px;
  transform: scale(1.1);
}

.cat {
  position: absolute;
  bottom: 7px;
  right: 7px;
  width: 10px;
  height: 10px;
  border-radius: 10px;
}

.cat-a {
  background: var(--catA);
}
.cat-a:hover {
  background: var(--catAtransp);
}
.cat-b {
  background: var(--catB);
}
.cat-b:hover {
  background: var(--catBtransp);
}
.cat-c {
  background: var(--catC);
}
.cat-c:hover {
  background: var(--catCtransp);
}
.cat-d {
  background: var(--catD);
}
.cat-c:hover {
  background: var(--catDtransp);
}

.green-task {
  background: #555f57;
}
.pink-task {
  background: rgb(121, 100, 99);
}
.blue-task {
  background: rgb(91, 103, 109);
}
.grey-task {
  background: #58595c;
}
.grey-task:hover {
  background: #494a4e;
}

.task-btn-delete,
.task-btn-hide {
  position: absolute;
  top: 6px;
  right: 6px;
  width: 24px;
  height: 24px;
  background: url(/static/media/bin.d132c0fb.svg) no-repeat center;
  background-size: 60%;
  border-radius: 2px;
  opacity: 0.7;
}

.task-btn-delete:hover,
.task-btn-hide:hover {
  opacity: 1;
  background-color: var(--bgDarkest);
  cursor: pointer;
  transition: all 0.5s ease;
}

.task-btn-delete:hover::after {
  content: "Delete";
  text-align: center;
  position: absolute;
  font-size: 0.7rem;
  top: 1px;
  left: 0;
  transform: translateX(-110%);
  animation: fadeIn 0.5s;
  animation-delay: 1s;
  animation-fill-mode: backwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.left-side {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  width: 25vw;
  min-width: 300px;
  max-width: 410px;
  height: 100vh;
  padding: calc(50px + 2vh) 12px;
  background: var(--bgDarker);
  box-sizing: border-box;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  z-index: 1;
  overflow-y: auto;
  overflow-x: hidden;
}

.left-side-top {
  display: flex;
  margin: 10px 0 5px;
  justify-content: space-between;
}

.side-top-h3 {
  position: relative;
  margin: 0;
  font-size: 1rem;
  color: #e8e8e8;
  transition: all 0.3s ease-in-out;
}

.title-drop:hover {
  cursor: pointer;
  margin-left: 5px;
}

.title-drop::after {
  content: "";
  display: block;
  position: absolute;
  top: 6px;
  right: -20px;
  width: 0;
  height: 0;
  border-radius: 2px;
  border-left: transparent 9px solid;
  border-right: transparent 9px solid;
  border-bottom: transparent 9px solid;
  border-top: #666 9px solid;
  transform: scaleX(0.8);
  transition: all 0.3s ease-in-out;
  /* border-left: salmon 10px solid;
  border-right: olive 10px solid;
  border-bottom: teal 10px solid;
  border-top: palegoldenrod 10px solid; */
}

.title-drop:hover::after {
  border-top: #bbb 9px solid;
}

